.footer_main{

    display: grid;
    grid-template-columns: 70% 30%;
    grid-template-rows: 100%;
    background-color: rgb(27, 27, 27);
    color: white;
    text-align: center;
    padding: 20px 50px 20px 50px;
    
}
ul li{
    list-style-type: none;
}
ul li a{
    display: block;
    text-decoration: none;
    
}
a:-webkit-any-link {
    text-decoration: none;
    color: white;
    cursor: pointer;
  }
a:hover{
    color: blue;
}
.footer_main p img{
    margin: 20px;
}

