.main_div{
    background-color: #f7f7f7;
    margin-bottom: 0px;
    min-height: 70vh;
    width: 100%;

}
.main_heading{
    text-align: center;
    color: gray;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.contact_div{
    display: flex inline-block;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: auto;
}

input[type="text"]{
    display: block;
    text-align: center;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    border-width: 0px;
    margin: auto;
    height: 50px;
    width: 25%;
    padding: 20px;
    margin-top: 20px;
}
input[type="number"]{
    display: block;
    text-align: center;
    justify-content: center;
    align-items: center;

    border-radius: 10px;
    border-width: 0px;
    margin: auto;
    height: 50px;
    width: 25%;
    padding: 20px;
    margin-top: 20px;
}

.btn_submit{
    margin: 20px;
    border-radius: 5px;
    font-size: large;
    font-weight: 500;    
}

.btn_submit:hover{
    background-color: black;
    color: white;
    font-size: larger;
}