.main_div1{
    width: 100%;
    height: 500px;
    background-repeat: no-repeat;
    background-position: right;
    background-color: #f7f7f7;
    background-size:100% 100%;
    display: flex;
    align-items: center;
    justify-content: left;
    margin: 0;
    

}
.main_div_inside1{
    width: 50%;
    text-align: center;
    margin: 0px;
}
.btn_home{
    border-radius: 5px;
    font-size: large;
    font-weight: 500;
}
.btn_home:hover{
    background-color: black;
    color: white;
    font-size: larger;
}




